export async function getDashboardData(mode: string) {
  return dummyData[mode] || [];
}

export async function getKPIs(mode: string) {
  return getDashboardData(`${mode}-kpis`);
}

export async function getErrorData(mode: string) {
  return getDashboardData(`${mode}-errors`);
}

export async function getPlotData(mode: string) {
  return getDashboardData(`${mode}-plots`);
}

export async function getXaiExplanation(mode: string) {
  const data = await getDashboardData(`${mode}-xai`);
  return data[0]?.text || '';
}